
	<div>
		
		<table>
			<thead>
				<tr>
					<th>Intended Learning Outcome</th>
					<th>Examined Elements</th>
					<th>Learning Activities</th>
				</tr>
			</thead>
			<tbody>
				<tr v-for="ilo in ilos" :key="ilo.text">
					<td>{{ilo.text}}</td>
					<td>
						<div v-for="test in ilo.tests" :key="test">
							{{test}}
						</div>
					</td>
					<td>
						<div v-for="activity in ilo.activities" :key="activity">
							{{activity}}
						</div>
					</td>
				</tr>
			</tbody>
		</table>
		
	</div>
